.about {
  // max-width: 1250px;
  // margin: auto;
  // background-color: rgb(255, 255, 255);
  padding: 1rem;
  height: 650px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  &-details {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 14rem;
    // background-color: blueviolet;
    padding: 1rem;
    &-image {
      img {
        width: 20rem;
      }
    }
    &-tag {
      font-size: 20px;
      margin-right: 0.5rem;
    }
    &-name {
      font-family: "Roboto Mono", monospace;
      font-size: 35px;
      font-weight: bold;
    }
    &-role {
      font-family: "Roboto Mono", monospace;
      margin: 1rem 0;
      font-size: 23px;
    }
    &-resume {
      border: none;
      background-color: rgb(15, 39, 194);
      padding: 10px;
      border-radius: 10px;
      width: 140px;
      height: 40px;
      margin-right: 25px;
      cursor: pointer;
      color: white;
      font-size: 16px;
      &:hover {
        background-color: rgb(75, 75, 75);
      }
    }
  }
}
