.header {
  display: flex;
  // justify-content: center;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  // background-color: $background_color;
  padding: 10px;
  width: 100%;
  height: 80px;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 1;
  &-logo {
    display: flex;
    align-items: center;
    &-text {
      font-size: 40px;
    }
    img {
      width: 10rem;
    }
  }
  &-bar {
    display: flex;
    justify-content: space-around;
    width: 45%;
    color: #ff3333;
    cursor: pointer;
    &-options {
      color: #c6c6c8;
      font-size: 22px;
      font-family: "Tilt Neon", sans-serif;
      &-active {
        color: #000000;
      }
      &:hover {
        color: #000000;
      }
    }
  }
  &-button {
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 50px;
    padding: 10px;
    cursor: pointer;
  }
  &-hamburger {
    display: none;
    &-dropdown {
      // position: absolute;
      background-color: #ffffff;
      padding: 1rem;
      width: 100%;
      max-height: 586px;
      // top: 70px;
      // right: 15px;
      // text-align: end;
      // border-radius: 10px;
      // .menu {
      // }
      ul {
        margin: 0;
        padding: 0;
        list-style: none;
        overflow: hidden;
        li {
          text-decoration: none;
          list-style: none;
          padding: 10px;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .header {
    padding: 1rem 0;
    margin: 1rem 0;
    justify-content: space-around;
    border-bottom: 1px solid;
    &-bar {
      display: none;
    }
    &-hamburger {
      clear: both;
      transition: max-height 5s ease-out;
      display: block;
    }
  }
}
